.projects-page {
  position: relative;
  height: 100vh;
  overflow: hidden;
  background-color: #ffffff;
}

.project-image {
  position: absolute;
  top: 10%;
  left: 20%;
  width: 20%;
  height: 80%;
  background-image: url("project.jpg");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.container-projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 10px;
  align-items: center;
  height: 100%;
  margin-left: 250px; 
}

.container-proj1 {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end; 
  height: 100vh;
  grid-gap: 10px;
}

.container-proj2 {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start; 
  height: 100vh;
  grid-gap: 10px;
}


.project-box {
  font-family: 'Arial', sans-serif;
  font-size: 18px;
  padding: 80px;
  background-color: #000000;
  color: #ffffff;
  border: none;
  border-radius: 10px;
  transition: background-color 0.7s ease;
  position: relative;
  overflow: hidden;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 260px;
  height: 200px;
  animation: zoomIn 1s ease-in-out;
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.project-box::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f9f8e7;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease;
  z-index: -1;
  color: #ffffff;
}

.project-box.hovered {
  background-color: #f9f8e7;
  color: #000000;
  transform: scale(1.05) rotateZ(0deg);
}

.project-box.hovered::before {
  transform: scaleX(1);
}

.project-box:hover {
  cursor: pointer;
}


@media (max-width: 800px) {
 
  .container-projects {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin-left: 100px; 
  }
  
  .container-proj {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
  }
  
  .project-box {
    font-family: 'Arial', sans-serif;
    font-size: 14px; /* Decreased font size for mobile devices */
    padding: 10px; /* Decreased padding for mobile devices */
    background-color: #000000;
    color: #ffffff;
    border: none;
    border-radius: 10px;
    transition: background-color 0.7s ease;
    position: relative;
    overflow: hidden;
    z-index: 1;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100px; /* Decreased width for mobile devices */
    height: 120px; /* Decreased height for mobile devices */
    animation: zoomIn 1s ease-in-out;
  }

  .project-image {
    position: absolute;
    top: 10%;
    left: 8%;
    width: 20%;
    height: 80%;
    background-image: url("project.jpg");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }
}
