@import url('https://fonts.googleapis.com/css?family=Roboto+Mono:100');

.about-page {
    position: relative;
    height: 100vh;
    overflow: hidden;
    background-color: #ffffff
  }

.text-about {
  font-weight: 100;
  font-size: 28px;
  color: #000000;
}

.dud-about {
  color: #757575;
}

.container-about {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20vh;
  margin-top: 40px;
}

.about-image {
  max-width: 100%;
  max-height: 100%;
}

/*cpu image*/

.container-cpu {
  display: flex;
  justify-content: center;
  align-items: flex-end; /* Align the content to the bottom */
  height: 50vh; /* Adjust the height as needed */
  margin-top: 200px; /* Add margin to create space between the image and the bottom of the page */
}

.cpu-image {
  max-width: 100%;
  max-height: 100%;
}


/*text for this */
.animate-character {
  text-transform: uppercase;
  background-image: linear-gradient(
    -225deg,
    #231557 0%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 2s linear infinite;
  display: inline-block;
  font-size: 50px;
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}


/* resume link */
.resume-link {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  margin-bottom: 60px;
}

.resume-link a {
  font-size: 18px;
  position: relative;
  text-decoration: none;
  color: #000; /* Set the desired text color */
  transition: color 0.3s ease; /* Add transition to color property */
}

.resume-link a::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px; /* Set the desired underline height */
  background-color: #000; /* Set the desired underline color */
  bottom: -5px; /* Adjust as needed to position the underline */
  opacity: 0; /* Initially hidden */
  transition: opacity 0.3s ease; /* Add transition to opacity property */
}

.resume-link a:hover {
  color: #00f; /* Set the desired text color on hover */
}

.resume-link a:hover::before {
  opacity: 1; /* Fade in the underline on hover */
}


.drawing-attribution {
  font-size: 11px;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  margin-bottom: 30px;
  color: #999999;
}

/*idek*/

@font-face {
  font-family: 'Outfit';
  src: url('../fonts/Outfit/Outfit-VariableFont_wght.ttf') format('truetype');
}

@keyframes slide-in {
  from {
    opacity: 0;
    transform: translateY(150%);
  }
  to {
    opacity: 1;
    transform: translateY(0%);
  }
}